<script>
import axios from 'axios';
export default {
    // edit by JoneElmo
    name: 'showContent',
    data() {
        return {
            arr: new Array(10),

            isShow: true,
            loginArgs: "",
            title: "个人登陆",

            phone: null,
            userEmail: null,
            password: null,
            userName: null,
            code: null,
            userId: null,

            isLogin: false,
            //模糊查询关键字
            word: "",

            HighRecruitList: "",
            // 领域列表 每一行的信息
            fields1: [],
            fields2: [],
            fields3: [],
            fields4: [],
            fields5: [],
            fields6: [],
            fields7: [],
